import React, { useState, useContext } from 'react'
import useUserDetails from '../../hooks/useUserDetails'
import { navigate } from 'hookrouter'
import signUp from '../../communication/signUp'
import UserContext from '../../context/UserContext'

export default () => {
  const setUser = useContext(UserContext)[1]
  const [attemptFailed, dispatch] = useState(false)
  const userDetails = useUserDetails()
  const { firstName, lastName, phone, phone2, email, password, agencyName } = userDetails
  const input = (type, state, text, placeholder = text) => (
    <div id="updateFieldContainer">
      <span>{text}:</span>
      <input
        type={type}
        onChange={e => state[1](e.target.value)}
        value={state[0]}
        placeholder={placeholder}
        className="filters__field-box-container"
      />
    </div>
  )
  const submitHandler = async e => {
    e.preventDefault()
    const user = await signUp(userDetails)
    if (!user) return dispatch(true)
    alert('  ')
    setUser(user)
    navigate(`/user`)
  }
  return (
    <div className="sign-up--form">
      <h2 className="align-self-center"></h2>
      <form onSubmit={e => submitHandler(e)}>
        {attemptFailed && <p>       *</p>}
        {input('text', firstName, '* ')}
        {input('text', lastName, '* ')}
        {input('text', phone, '*')}
        {input('text', phone2, ' 2')}
        {input('text', email, '*')}
        {input('password', password, '*', '  ')}
        {input('text', agencyName, ' ')}
        <div id='signUpButtonContainer'>
          <button tabIndex="0"></button>
        </div>
      </form>
    </div>
  )
}
